* {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  text-decoration: none;
  list-style: none;
}

html, body {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.wrapper {
  max-width: 1260px;
  margin: 0 auto;
  overflow: hidden;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding: 30px 25px;
  display: flex;
  position: relative;
}

.logo__img {
  transition: transform .3s;
}

.logo__img:hover {
  transform: scale(1.1);
}

@media (width >= 1024px) {
  .burger {
    display: none;
  }
}

.burger__open {
  cursor: pointer;
  background-image: url("burgerOpen.6f7db571.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 16px;
  transition: transform .3s;
}

.burger__open.rotated {
  transform: rotate(180deg);
}

.burger__close {
  cursor: pointer;
  background-image: url("burgerClose.417bedc0.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 16px;
  display: none;
}

.menu {
  z-index: 10;
  opacity: 0;
  background-color: #f9f9f9;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 0 25px;
  transition: transform .5s, opacity .5s;
  display: flex;
  position: absolute;
  top: 74px;
  bottom: 30px;
  left: 0;
  transform: translateX(100%);
}

.menu--active {
  opacity: 1;
  transform: translateX(0);
}

@media (width >= 1024px) {
  .menu {
    display: none;
  }
}

@media (width >= 728px) {
  .menu {
    top: 80px;
  }
}

